*:focus {
    outline: none;
}

.touchscreenTextInput {
    border: 1px solid #cccccc;
    border-radius: 5px;
    width: calc(100% - 30px);
    font-family: "Nimbus Sans L", "Arial Narrow", sans-serif;
    font-size: 2.2em;
    background-color: #dcdcdc;
    color: #000000;
    padding: 8px;
}


